.App {
  text-align: center;
}

.main_wrapper {
  display: flex;
  width: 100vw;
  height: 85vh;
}

.main {
  margin: auto;
  transition: margin 1s;
}

.title {
  font-family: 'Oswald', sans-serif;
  font-size: 84px;
}

.subtitle {
  margin-bottom: 20px;
  font-family: 'Roboto', sans-serif;
  font-size: 30px;
}

.warning {
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: bold;
}

#url_input {
  width: 600px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
}

.url_submit {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  margin-top: 8px;
}

.loading {
  font-weight: bold;
  font-style: italic;
  font-size: 16px;
  margin: 8px;
}

.streams {
  display: flex;
}

.stream_wrapper {
  margin: 35px 25px;
}

.stream_name {
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: bold;
}

.stream_desc {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  margin: 6px 0px;
}

.explanation1 {
  margin: 10px;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: bold;
}

.explanation2 {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

.stream_interface {
  font-size: 0;
}
